@charset "UTF-8";
html,
body {
  margin: 0;
  padding: 0;
  font-family: PingFangSC-Light,'helvetica neue','hiragino sans gb',tahoma,'microsoft yahei ui','microsoft yahei',simsun,sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  text-align: left; }

a {
  text-decoration: none;
  color: #333; }

a:focus,
input:focus {
  outline: none; }

ul,
li,
p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0; }

ul,
li {
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal; }

.container {
  width: 1140px;
  padding: 0;
  margin: 0 auto; }

*, ::after, ::before {
  box-sizing: border-box; }

a {
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
  color: #3389e2; }

.header-anchor {
  display: none; }

pre {
  padding: 1.2em !important;
  margin: 1.2em 0 !important;
  border-radius: 5px;
  background: #282c34; }
  pre code {
    color: #b8b8b8;
    background-color: transparent;
    padding-left: 0; }

code {
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace !important;
  line-height: 1.4;
  color: #666666;
  background-color: #ebf4fe;
  border-radius: 3px;
  padding: 1px 4px;
  font-size: 14px; }

p,
ul,
ol {
  line-height: 1.65; }

hr {
  border: 0;
  border-top: 1px solid #dfdfdf; }

img {
  vertical-align: middle;
  border-style: none; }

.custom-block {
  position: relative; }
  .custom-block:before {
    content: "!";
    position: absolute;
    width: 25px;
    height: 25px;
    left: -15px;
    top: 15px;
    text-align: center;
    line-height: 25px;
    border-radius: 50%;
    color: #fff; }
  .custom-block.tip, .custom-block.warning, .custom-block.danger {
    padding: .1rem 1.5rem;
    margin: 1rem 0;
    border-left: .3rem solid; }
  .custom-block.tip {
    background-color: #eeeeee;
    border-color: #499d7f; }
    .custom-block.tip:before {
      background-color: #499d7f; }
  .custom-block.warning {
    background-color: #ffeacc;
    border-color: #cb7900;
    color: #cb7900; }
    .custom-block.warning:before {
      background-color: #cb7900; }
  .custom-block.danger {
    background-color: #ffdede;
    border-color: #cc4949;
    color: #cc4949; }
    .custom-block.danger:before {
      background-color: #cc4949; }

blockquote {
  background: #f5f2f0;
  padding: 1em;
  margin: 2em;
  font-family: Consolas, Monaco, 'Andale Mono', monospace;
  border-radius: 1em;
  border-width: 3px solid #e0dfcc;
  text-shadow: 0 1px white;
  overflow: auto; }
  blockquote p {
    margin: 0; }

table {
  border-collapse: collapse;
  margin: 1rem 0;
  max-width: 100%; }
  table tr {
    border-top: 1px solid #e7e7e7; }
    table tr:nth-child(2n) {
      background-color: #dbdbdb; }
  table th,
  table td {
    border: 1px solid #e7e7e7;
    padding: 0.6em 1em; }

.doc-content .header-anchor {
  font-size: 0.85em;
  float: left;
  margin-left: -0.8em;
  padding-right: 0.23em;
  margin-top: 0.125em;
  opacity: 0;
  text-decoration: none;
  color: #2e7aca;
  cursor: pointer; }

.doc-content .table-container {
  overflow-x: auto; }

.doc-content h1,
.doc-content h2,
.doc-content h3,
.doc-content h4 {
  color: #444;
  margin: .65em 0; }
  .doc-content h1:hover .header-anchor,
  .doc-content h2:hover .header-anchor,
  .doc-content h3:hover .header-anchor,
  .doc-content h4:hover .header-anchor {
    opacity: 1; }

.doc-content p {
  margin: 1.6em 0; }

.doc-content ol, .doc-content ul, .doc-content dl {
  margin-top: 0;
  margin-bottom: 1rem; }

.doc-content img {
  vertical-align: middle;
  max-width: calc(100% - 50px);
  border: 0;
  border-radius: 4px;
  margin: 0 25px;
  box-shadow: 0px 8px 16px #999999; }

/*过渡效果*/
.fade-enter-active,
.fade-leave-active {
  transition: opacity .2s; }

.fade-enter,
.fade-leave-to {
  opacity: 0; }

.particles-js-canvas-el {
  position: absolute;
  top: 0;
  left: 0;
  right: 0; }
