.header {
  height: 60px;
  margin: 0 auto;
  background: #409eff; }
  .header .logo {
    float: left;
    height: 60px; }
  .header .nav-list {
    float: right; }
    .header .nav-list .list-item {
      float: left;
      font-size: 16px;
      line-height: 60px;
      padding-bottom: 3px;
      padding: 0 22px; }
      .header .nav-list .list-item a {
        color: #333;
        transition: color .3s; }
        .header .nav-list .list-item a:hover {
          color: #fff; }
      .header .nav-list .list-item .active-item {
        padding-bottom: 5px;
        color: #fff;
        border-bottom: 3px solid #fff; }
  .header .el-color-picker__trigger {
    margin-top: 12px; }
  .header .el-color-picker__panel {
    right: 20px; }
